<template>
<div id="register" class="box auth">
    <div class="title">REGISTER</div>
      <form id="form" v-on:submit.prevent="register()">
        <div class="form-group">
          <label for="username">Username</label>
          <input type="text" id="username" placeholder="   ">
        </div>
        <div class="form-group">
          <label class="Label" for="password">Password</label>
          <input type="password" id="password" placeholder="  ">
        </div>
        <div class="form-group">
          <label class="Label" for="password2">Reconfirm</label>
          <input type="password" id="password2" placeholder=" ">
        </div>
        <div class="form-group">
          <label class="Label" for="nickname">Nickname</label>
          <input type="text" id="nickname" placeholder=" ">
        </div>
        <div class="btn-group">
          <input class="btn" type="submit" value="SIGN UP">
          <input class="btn" type="button" onclick="location.href = '/auth/login'" value="SIGN IN">
        </div>
      </form>
      <a href="/" class="btn-home">Home @ usicMusic</a>
</div>
</template>

<script>
export default {
  name: 'register',
  methods: {
    register: function () {
      const username = document.getElementById('username').value
      const password = document.getElementById('password').value
      const password2 = document.getElementById('password2').value
      const nickname = document.getElementById('nickname').value

      if (username === '') {
        this.$js.alert('  ', null, this.$js.Icons.Warning, '')
      } else if (password === '') {
        this.$js.alert(' ', null, this.$js.Icons.Warning, '')
      } else if (password2 === '') {
        this.$js.alert(' ', null, this.$js.Icons.Warning, '')
      } else if (password !== password2) {
        this.$js.alert('  ', null, this.$js.Icons.Warning, '')
      } else if (nickname === '') {
        this.$js.alert(' ', null, this.$js.Icons.Warning, '')
      } else {
        this.$http.post('/api/auth/register', {username, password, nickname}).then((response) => {
          console.log(response)
          this.$js.alert('  ', ' ', this.$js.Icons.Success, '').then(() => {
            location.href = '/auth/login'
          })
        }).catch(error => {
          console.log(error)
        })
      }
    }
  }
}
</script>

<style scoped>
</style>



